#scroll-box {
  max-height: 80%;
  width: 60%;
  margin: 0 auto;

  overflow-x: hidden;
  overflow-y: auto;

  background-color: darkgrey;
  padding: 20px;
}

.port-title {
  margin-bottom: 0;
}

.port-desc {
  margin-top: 0;
}

.port-item {
	display: -moz-flex;
	display: -ms-flex;
	display: -ms-flexbox;
	display: flex;

  max-height: 300px;
  margin-top: 15px;
  margin-bottom: 15px;
}

.port-item img {
  border-radius: 0;
  width: 300px;
}

.port-item .content {
  overflow-y: auto;
  padding-left: 20px;
  padding-right: 20px;
	-moz-order: 2;
	-ms-order: 2;
	-ms-flex-order: 2;
	    order: 2;
	max-width: 48em;
	width: 100%;
}

.content::-webkit-scrollbar {
  display: none;
}

.port-item:nth-child(2n) {
	-moz-flex-direction: row-reverse;
	-ms-flex-direction: row-reverse;
	flex-direction: row-reverse;

  /* text-align: right; */
}



