#menu-wrapper {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

#menu-table {
  width: 80%;
  margin: 0 auto;
  table-layout: fixed;
  text-align: center;
}

.menu-text:hover {
  background-color: white;
}

.menu-text {
  -webkit-animation-name: underlineflash;
          animation-name: underlineflash;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate-reverse;
          animation-direction: alternate-reverse;
}

@-webkit-keyframes underlineflash {
  0%    {color: black;}
  25%   {color: grey;}
  50%   {color: black;}
  75%   {color: black;}
  100%  {color: black;}
}

@keyframes underlineflash {
  0%    {color: black;}
  25%   {color: grey;}
  50%   {color: black;}
  75%   {color: black;}
  100%  {color: black;}
}



